import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { SldscheduleService } from '../../services/sldschedule.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

import { SldSchedule } from './sld-schedule';

@Component({
  selector: 'app-sld-schedule-list',
  templateUrl: './sld-schedule-list.component.html',
  styleUrls: ['./sld-schedule-list.component.css']
})
export class SldScheduleListComponent implements OnInit {
  projectId: String;
  sldSchedules: Object;
  sldSchedule: any;

  //sldSchedule: SldSchedule[];

  constructor(
    private sldscheduleService: SldscheduleService,
    private router: Router,
    private route: ActivatedRoute,
    private spinnerService: Ng4LoadingSpinnerService
  ) {this.projectId = this.route.snapshot.params['id']; }

  ngOnInit() {
    this.spinnerService.show();
    this.sldscheduleService.getSldSchedule(this.projectId).subscribe(sldSchedulesList => {
      this.sldSchedules = sldSchedulesList;
    },
    err => {
      console.log(err);
      return false;
    });
    this.spinnerService.hide();
  }

  saveSldShedule() {
    this.sldSchedule = {};
    this.sldSchedule.length = 0;
    this.spinnerService.show();
    this.sldscheduleService.createSldSchedule(this.projectId, this.sldSchedule).subscribe(
      (res: Array<string>) => {
        let id = res['_id'];
        let responseId = res[res.length-1]['_id']
        let routeToCableItem = '/project/'+this.route.snapshot.params['id']+'/sldshedules/'+responseId;
        this.router.navigate([routeToCableItem]);
      }, (err) => {
        console.log(err);
      }
    );
    this.spinnerService.hide();
  }
}



